import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import { convertFromHTML } from "draft-js";
import {
  Box,
  Divider,
  Container,
  Button,
  Paper,
  TableContainer,
  Table,
  TableCell,
  TableRow,
  Link,
  CircularProgress,
} from "@material-ui/core";
import { Helmet } from "react-helmet";
import { useParams } from "react-router-dom";
import axios from "axios";
import Moment from "react-moment";
const useStyles = makeStyles({
  root: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    placeItems: "center",
    margin: "85px 20px 20px 20px",
  },
  card: {
    margin: "20px",
  },
  media: {
    height: 140,
  },
});

export default function JobDescription() {
  const classes = useStyles();
  const params = useParams();

  const [job, setJob] = React.useState({});
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState("");

  useEffect(() => {
    // fetch from server from route /api/list/:id
    axios
      .get(`/api/list/${params.id}`)
      .then((res) => {
        setJob(res.data);
        setLoading(false);
        console.log(res.data);
      })
      .catch((err) => {
        console.log(err);
        setError(true);
        setErrorMessage(err.message);
      });
  }, []);

  function renderRequirement() {
    return {__html: job.requirements};
  }
  function renderDescription() {
    return {__html: job.description};
  }
  return (
    <>
      <Helmet>
        <meta
          name="description"
          content={`Job opening for ${job.title} for ${job.applicant_no} at Rupandehi Job`}
        />
        <title> {`${job.title}`} - Rupandehi Job</title>
      </Helmet>
      <Box justifyContent="center" className={classes.root}>
        <Card className={classes.card}>
          <CardActionArea>
            <CardMedia
              className={classes.media}
              image="/assets/images/rupandehijob.jpg"
              title="rupandehi job"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="h2">
                A Reputed Company
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                Lizards are a widespread group of squamate reptiles, with over
                6,000 species, ranging across all continents except Antarctica
              </Typography>
            </CardContent>
          </CardActionArea>
        </Card>
        <Box>
          <Card className={classes.card}>
            <CardContent>
              <Container>
                <CardContent>
                  <Typography variant="h5" component="strong">
                    Basic Job Information
                  </Typography>
                </CardContent>
                <CardContent>
                  <TableContainer component={Paper}>
                    <Table>
                      <tbody>
                        <TableRow>
                          <TableCell width="33%">Job Category</TableCell>
                          <TableCell width="3%">:</TableCell>
                          <TableCell width="64%">
                            <Link href="/category/teaching-education/">
                              {job.category}
                            </Link>
                          </TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>Job Level</TableCell>
                          <TableCell>:</TableCell>
                          <TableCell>
                            <Link href="/job-level/mid_level/">
                              {job.level}
                            </Link>
                          </TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>No. of Vacancy/s</TableCell>
                          <TableCell>:</TableCell>
                          <TableCell>
                            [ <strong>{job.applicant_no}</strong> ]
                          </TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>Employment Type</TableCell>
                          <TableCell>:</TableCell>
                          <TableCell itemProp="employmentType">
                            {job.type}
                          </TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>Job Location</TableCell>
                          <TableCell>:</TableCell>
                          <TableCell>
                            <span>{job.job_location}</span>
                          </TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>Offered Salary</TableCell>
                          <TableCell>:</TableCell>
                          <TableCell>{job.salary}</TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>
                            Apply Before<span>(Deadline)</span>
                          </TableCell>
                          <TableCell>:</TableCell>
                          <TableCell>
                            <Moment format="DD-MM-YYYY, hh:mm:ss">
                              {job.deadline}
                            </Moment>
                            <Divider orientation="vertical" />
                            <Moment durationFromNow>{job.deadline}</Moment>
                          </TableCell>
                        </TableRow>
                      </tbody>
                    </Table>
                  </TableContainer>
                </CardContent>
              </Container>
            </CardContent>
            <Divider className="mt-0 mb-4" />

            <CardContent>
              <Container>
                <CardContent>
                  {" "}
                  <Typography variant="h5" component="strong">
                    Job Specification
                  </Typography>{" "}
                </CardContent>
                <CardContent>
                  <Table>
                    <tbody>
                      <TableRow>
                        <TableCell width="33%">Education Level</TableCell>
                        <TableCell width="3%">:</TableCell>
                        <TableCell width="64%">
                          <span itemProp="educationRequirements">
                            {job.edu_level}
                          </span>
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell width="33%">Experience Required</TableCell>
                        <TableCell width="3%">:</TableCell>
                        <TableCell width="64%">
                          <span itemProp="experienceRequirements">
                            {job.experience}
                          </span>
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell width="33%">
                          Professional Skill Required
                        </TableCell>
                        <TableCell width="3%">:</TableCell>
                        <TableCell width="64%">
                          <Typography
                            variant="body1"
                            component="span"
                            itemProp="skills"
                          >
                            {job.skills &&
                              job.skills.map((skill) => (
                                <span key={skill}>
                                  <Typography
                                    color="secondary"
                                    variant="subtitle2"
                                    component="span"
                                  >
                                    {skill}{" "}
                                  </Typography>
                                </span>
                              ))}
                          </Typography>
                        </TableCell>
                      </TableRow>
                    </tbody>
                  </Table>
                  <CardContent>
                    <Container>
                      <CardContent>
                        <Typography variant="h6" component={"strong"}>
                          Other Specification
                        </Typography>
                      </CardContent>
                      <CardContent>
                        <Container>
                          <Typography>
                            <strong>Requirements</strong>
                          </Typography>
                          <Container>
                            <Typography>
                              <div itemProp="requirements" dangerouslySetInnerHTML={renderRequirement()} >
                                
                              </div>
                            </Typography>
                          </Container>
                        </Container>
                      </CardContent>
                    </Container>
                  </CardContent>
                </CardContent>
              </Container>
            </CardContent>
            <Divider />

            <CardContent>
              <Container>
                <CardContent>
                  <Typography variant="h6" component="p">
                    {" "}
                    Job Description
                  </Typography>
                  <Container itemProp="description">
                    <div dangerouslySetInnerHTML={renderDescription()} />
                    <p>
                      <Typography component="strong">
                        Applying Procedure:
                      </Typography>
                    </p>
                    <p>
                      Interested Candidates must send detailed CV with Cover
                      letter to
                      <strong>
                        &nbsp;
                        <Link href="mailto:info@maTableRowibhumischool.edu.np">
                          info@maTableRowibhumischool.edu.np
                        </Link>
                      </strong>
                    </p>
                    <p>
                      <strong>OR,</strong>
                    </p>
                  </Container>
                </CardContent>
              </Container>
              <Divider />

              <Container>
                <Button variant="contained" size="small" color="secondary">
                  APPLY NOW
                </Button>
              </Container>
            </CardContent>
          </Card>
        </Box>
      </Box>
    </>
  );
}
